@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{display:flex;flex-direction:column;align-items:center;justify-content:center;font-family:"Montserrat", sans-serif}.App{margin-top:30vh;padding:30px;box-shadow:0px 1px 1px 0px rgba(0,0,0,0.5);-webkit-box-shadow:0px 1px 1px 0px rgba(0,0,0,0.5);-moz-box-shadow:0px 1px 1px 0px rgba(0,0,0,0.5);border-radius:4px;min-width:400px}.todo-input{display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;width:195px;margin-bottom:30px;width:100%}.todo-input .input{padding:10px 10px;width:70%}.todo-input .input:focus{outline:none !important;border:1px solid #2986a6}.todo-input .PlusBtn{color:#2986a6}.todo-input .PlusBtn:hover{cursor:pointer}.todo{background-color:#2986a6;display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:auto;margin-bottom:10px;padding:10px;color:#fff;width:100%;border-radius:7px;word-wrap:break-word}.todo .right>*{margin-right:10px}.todo .btns>*{margin-right:10px}.todo .EditBtn:hover{cursor:pointer}.todo .DeleteBtn:hover{cursor:pointer}.todolist{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}

